import React, { useContext, useState } from 'react';

import { cloneDeep } from 'lodash';
import { compress as lzStringCompress } from 'lz-string';

import { Box, Typography } from '@mui/material';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import DoneIcon from '@mui/icons-material/Done';
import EditIcon from '@mui/icons-material/Edit';
import IconButton from '@mui/material/IconButton';
import TextField from '@mui/material/TextField';

import { CLASS_INFO_KEY, USER_INPUT_KEY } from "../utils/storage-utils";

import { AppContext } from "../App";

const StudentItem = ({studentName, studentId, classId, disableDelete}) => {
  const { classInfoContext, currentUserInputContext, currentClassIDContext, currentStudentIDContext } = useContext(AppContext);
  const [currentUserInput, setCurrentUserInput] = currentUserInputContext;
  const [classInfo, setClassInfo] = classInfoContext;
  const [currentClassID] = currentClassIDContext;
  const [currentStudentID, setCurrentStudentID] = currentStudentIDContext;

  const [editingStudent, setEditingStudent] = useState(false);
  const [editableStudent, setEditableStudent] = useState(studentName);

  const toggleEditingStudent = (editing) => {
    if (editing) {
      setEditingStudent(true);
    } else {
      handleDoneEditingStudent();
    }
  }

  const handleChangeEditableStudent = (event) => {
    setEditableStudent(event.target.value);
  };

  const handleDoneEditingStudent = () => {
    setEditingStudent(false);
    let updatedClassInfo = cloneDeep(classInfo);
    const cIndex = classInfo.findIndex((c) => c.id === classId);
    const sIndex = classInfo[cIndex].students.findIndex((s) => s.id === studentId);

    if (editableStudent === "") {
      setEditableStudent(updatedClassInfo[cIndex].students[sIndex].name);
      return;
    }

    if (cIndex >= 0 && sIndex >= 0) {
      updatedClassInfo[cIndex].students[sIndex].name = editableStudent;
      setClassInfo(updatedClassInfo);
      localStorage.setItem(CLASS_INFO_KEY, lzStringCompress(JSON.stringify(updatedClassInfo)));
    }
  };

  const handleDeleteStudent = () => {
    let updatedClassInfo = cloneDeep(classInfo);
    const cIndex = classInfo.findIndex((c) => c.id === classId);
    const sIndex = classInfo[cIndex].students.findIndex((s) => s.id === studentId);
    if (cIndex >= 0 && sIndex >= 0) {
      updatedClassInfo[cIndex].students.splice(sIndex, 1);
    }
    setClassInfo(updatedClassInfo);
    localStorage.setItem(CLASS_INFO_KEY, lzStringCompress(JSON.stringify(updatedClassInfo)));

    let updatedCurrentUserInput = cloneDeep(currentUserInput);
    delete updatedCurrentUserInput.cls[classId][studentId];
    setCurrentUserInput(updatedCurrentUserInput);
    localStorage.setItem(USER_INPUT_KEY, lzStringCompress(JSON.stringify(updatedCurrentUserInput)));

    if (currentClassID === classId && currentStudentID === studentId) {
      setCurrentStudentID(updatedClassInfo[cIndex].students[0].id);
    }
  };

  return (
    <Box
      sx={{margin:"2px", justifyContent:"center", width:"100%"}}
      display="flex"
      flexDirection="row"
      alignItems="center"
    >
      <Box
        sx={{margin:"2px", justifyContent:"center"}}
        display="flex"
        flexDirection="row"
        alignItems="center"
      >
        {!editingStudent
        ? <Typography sx={{minWidth:"270px", borderBottom:"1px solid", fontSize:"0.8rem"}}>
          {studentName}
        </Typography>
        : <TextField
          id="student-name"
          variant="outlined"
          size="small"
          value={editableStudent}
          sx={{minWidth: "270px"}}
          inputProps={{style: {fontSize:"0.8rem", height:"11px"}}}
          onChange={(event) => {
            handleChangeEditableStudent(event);
          }}
        />}
      </Box>
      <Box
        sx={{margin:"2px", marginLeft:"auto"}}
        display="flex"
        flexDirection="row"
        alignItems="center"
      >
        <IconButton
          size="large"
          edge="start"
          color="black"
          aria-label="menu"
          sx={{margin:"2px 8px 2px 4px", padding:"0"}}
          onClick={() => toggleEditingStudent(!editingStudent)}
        >
          {editingStudent ? <DoneIcon /> : <EditIcon />}
        </IconButton>
        <IconButton
            disabled={disableDelete}
            size="large"
            edge="start"
            color="black"
            aria-label="menu"
            sx={{margin:"2px 4px 2px 8px", padding:"0"}}
            onClick={handleDeleteStudent}
          >
            <DeleteForeverIcon />
        </IconButton>
      </Box>
    </Box>
  );
}

export default StudentItem;