import TextField from '@mui/material/TextField';

const NoteEntry = ({currentUserInput, classID, studentID, rubricID, questionID, dimensionID, handleChangeNotes}) => {
  return (
    <TextField
      id="notes"
      label="notes"
      variant="outlined"
      value={currentUserInput?.cls?.[classID]?.[studentID]?.[rubricID]?.[questionID]?.[dimensionID]?.n || ""}
      sx={{width: "100%", margin:"12px 0 0 0"}}
      size={"small"}
      inputProps={{style: {fontSize: "0.875rem"}}}
      onChange={(event) => {
        handleChangeNotes(event);
      }}
    />
  );
}

export default NoteEntry;
