import * as React from 'react';
import { useContext } from 'react';
import { useNavigate } from "react-router-dom";

import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import { Box, Button, Typography } from '@mui/material';
import AppBar from '@mui/material/AppBar';
import FormControl from '@mui/material/FormControl';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import MenuIcon from '@mui/icons-material/Menu';
import Select from '@mui/material/Select';

import LOGO from '../images/scale_science_logo.webp';

import { AppContext } from '../App';

const TopBar = ({showClassSelection, showRubricSelection}) => {
  let navigate = useNavigate();
  const routeChange = (newRoute) =>{
    let path = newRoute;
    navigate(path);
  }
  const navigateToRubric = () =>{
    let path = `/rubric/${currentRubricID}`;
    navigate(path);
  }

  const { classInfoContext, currentClassIDContext, currentRubricIDContext, rubricsContext, currentStudentIDContext } = useContext(AppContext);
  const [rubrics] = rubricsContext;
  const [classInfo] = classInfoContext;
  const [currentClassID, setCurrentClassID] = currentClassIDContext;
  const [currentRubricID, setCurrentRubricID] = currentRubricIDContext;
  const [, setCurrentStudentID] = currentStudentIDContext;

  const handleChangeClass = (event) => {
    const classID = event.target.value;
    setCurrentClassID(classID);
    const cIndex = classInfo.findIndex((c) => c.id === classID);
    setCurrentStudentID(classInfo[cIndex].students[0].id);
  };

  const handleChangeRubric = (event) => {
    setCurrentRubricID(event.target.value);
    let path = `/rubric/${event.target.value}`;
    navigate(path);
  };

  const theme = useTheme();
  const isMedium = useMediaQuery(theme.breakpoints.down('md'));

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar position="static" sx={{ backgroundColor: "white" }}>
      <Toolbar>
        <img src={LOGO}
          alt="scale science logo"
          style={{margin:"10px", pointerEvents:"all", cursor:"pointer"}}
          onClick={() => routeChange("/")}
        />
        { isMedium
        ? <Box sx={{marginLeft:"auto"}}>
          <IconButton
            size="large"
            edge="start"
            color="black"
            aria-label="menu"
            sx={{ mr: 2 }}
            onClick={handleClick}
          >
            <MenuIcon />
          </IconButton>
          <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            MenuListProps={{
              'aria-labelledby': 'basic-button',
            }}
          >
            <MenuItem onClick={() => routeChange("/")}>Home</MenuItem>
            <MenuItem onClick={() => routeChange("/class")}>Class</MenuItem>
            <MenuItem onClick={navigateToRubric}>Rubric</MenuItem>
            <MenuItem onClick={() => routeChange("/guide")}>How-to</MenuItem>
            <MenuItem onClick={() => routeChange("/about")}>About</MenuItem>
            <MenuItem onClick={() => routeChange("/privacy")}>Privacy</MenuItem>
          </Menu>
        </Box>
        : <Box sx={{marginLeft:"auto"}}>
          <Button
            variant="text"
            sx={{margin: "0", color: "black", "&:hover": {textDecoration: "underline"}}}
            onClick={() => routeChange("/")}
          >
            Home
          </Button>
          <Button
            variant="text"
            sx={{margin: "0", color: "black", "&:hover": {textDecoration: "underline"}}}
            onClick={() => routeChange("/class")}
          >
            Class
          </Button>
          <Button
            variant="text"
            sx={{margin: "0", color: "black", "&:hover": {textDecoration: "underline"}}}
            onClick={navigateToRubric}
          >
            Rubric
          </Button>
          <Button
            variant="text"
            sx={{margin: "0", color: "black", "&:hover": {textDecoration: "underline"}}}
            onClick={() => routeChange("/guide")}
          >
            How-To
          </Button>
          <Button
            variant="text"
            sx={{margin: "0", color: "black", "&:hover": {textDecoration: "underline"}}}
            onClick={() => routeChange("/about")}
          >
            About
          </Button>
          <Button
            variant="text"
            sx={{margin: "0", color: "black", "&:hover": {textDecoration: "underline"}}}
            onClick={() => routeChange("/privacy")}
          >
            Privacy
          </Button>
        </Box> }
      </Toolbar>
      <Toolbar variant="dense" sx={{backgroundColor:"#0E487E"}}>
        {showClassSelection &&
          <>
            <Typography variant={"body2"} component={"div"}>Current Class</Typography>
            <Box sx={{backgroundColor:"white", padding:"6px", marginLeft:"10px", borderRadius:"4px"}}>
              <FormControl variant="standard" size="small" sx={{minWidth:"200px", backgroundColor:"white"}}>
                <Select
                  labelId="class-select-label"
                  id="class-select"
                  value={currentClassID}
                  label="Class"
                  onChange={handleChangeClass}
                >
                  {classInfo.map((c, i) =>
                    <MenuItem key={`class${i}`} value={c.id}>{c.name}</MenuItem>
                  )}
                </Select>
              </FormControl>
            </Box>
          </>
        }
        {showRubricSelection &&
          <>
            <Typography variant={"body2"} component={"div"} sx={{marginLeft:"20px"}}>Current Rubric</Typography>
            <Box sx={{backgroundColor:"white", padding:"6px", marginLeft:"10px", borderRadius:"4px"}}>
              <FormControl variant="standard" size="small" sx={{minWidth:"300px", backgroundColor:"white"}}>
                <Select
                  labelId="rubric-select-label"
                  id="rubric-select"
                  value={currentRubricID}
                  label="Rubric"
                  onChange={handleChangeRubric}
                >
                  {rubrics.map((r, i) =>
                    <MenuItem key={`rubric${i}`} value={r.r_id}>{r.name}</MenuItem>
                  )}
                </Select>
              </FormControl>
            </Box>
          </>
        }
      </Toolbar>
    </AppBar>
  );
}

export default TopBar;