import React, { useContext, useState }  from 'react';

import { cloneDeep } from 'lodash';
import { compress as lzStringCompress } from 'lz-string';

import { Box, Button, Card, CardContent, Container, Typography } from '@mui/material';
import Link from '@mui/material/Link';
import Stack from '@mui/material/Stack';

import { defaultClass } from '../utils/utils';
import { CLASS_INFO_KEY } from '../utils/storage-utils';

import { AppContext } from '../App';

import ClassCard from '../components/ClassCard';
import ModalPopUp from '../components/ModalPopUp';
import TopBar from '../components/TopBar';

export default function ClassManager() {
  const { classInfoContext, currentUserInputContext, rubricsContext } = useContext(AppContext);
  const [classInfo, setClassInfo] = classInfoContext;
  const [currentUserInput, setCurrentUserInput] = currentUserInputContext;
  const [rubrics] = rubricsContext;

  const [openModal, setOpenModal] = useState(false);
  const [modalHeaderText, setModalHeaderText] = useState("");
  const [modalBodyText, setModalBodyText] = useState("");
  const handleOpenModal = () => setOpenModal(true);
  const handleCloseModal = () => setOpenModal(false);

  const showModal = (headerText, contentText) => {
    setModalBodyText(contentText);
    setModalHeaderText(headerText);
    handleOpenModal();
  }

  const handleAddClass = (event) => {
    let updatedClassId = 0;
    for (let i = 0; i < classInfo.length; i++) {
      if (updatedClassId <= classInfo[i].id)
        updatedClassId = classInfo[i].id + 1;
    }
    let updatedClass = cloneDeep(defaultClass);
    updatedClass.id = updatedClassId;
    let updatedClassInfo = cloneDeep(classInfo);
    updatedClassInfo.push(updatedClass);
    setClassInfo(updatedClassInfo);
    localStorage.setItem(CLASS_INFO_KEY, lzStringCompress(JSON.stringify(updatedClassInfo)));

    // update user input to include new class
    let updatedCurrentUserInput = cloneDeep(currentUserInput);
    updatedCurrentUserInput.cls[updatedClass.id] = {};
    for (let s = 0; s < updatedClass.students.length; s++) {
      const sitem = updatedClass.students[s];
      updatedCurrentUserInput.cls[updatedClass.id][sitem.id] = {};
      for (let r = 0; r < rubrics.length; r++) {
        const ritem = rubrics[r];
        updatedCurrentUserInput.cls[updatedClass.id][sitem.id][ritem.r_id] = {};
        for (let q = 0; q < ritem.questions.length; q++) {
          const qitem = ritem.questions[q];
          updatedCurrentUserInput.cls[updatedClass.id][sitem.id][ritem.r_id][qitem.q_id] = {};
          for (let d = 0; d < qitem.dimensions.length; d++) {
            const ditem = qitem.dimensions[d];
            updatedCurrentUserInput.cls[updatedClass.id][sitem.id][ritem.r_id][qitem.q_id][ditem.d_id] = { n: "" };
            for (let p = 0; p < ditem.description_performance.points.length; p++) {
              const pitem = ditem.description_performance.points[p];
              updatedCurrentUserInput.cls[updatedClass.id][sitem.id][ritem.r_id][qitem.q_id][ditem.d_id][pitem.p_id] = { v: 0};
            }
          }
        }
      }
    }
    setCurrentUserInput(updatedCurrentUserInput);
  };

  return (
    <div>
      <ModalPopUp
        open={openModal}
        handleClose={handleCloseModal}
        headerText={modalHeaderText}
        bodyText={modalBodyText}
      />
      <TopBar />
      <Container sx={{width:"100%", boxSizing:"border-box", "&.MuiContainer-root": {maxWidth:"100%"}}}>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={5}
          sx={{width:"auto"}}
        >
          <Card elevation={2} sx={{outline:"1px solid gainsboro", width:"100%"}}>
            <CardContent sx={{width:"100%"}}>
              <Typography variant={"h5"} component={"h5"} m={1}>
                Class Manager
              </Typography>
              <Box m={2}>
                <p>You can create multiple classes with a unique student roster for each class. Use the ADD CLASS button to add a class.
                  Once a class has been created, you can edit the class name or student roster or delete the class entirely.
                  Use the <Link href="#options">Options and Settings page</Link> for additional features.</p>
              </Box>
              <Box p={2} display="flex" flexDirection="row" flexWrap={"wrap"}>
                {classInfo.map((c, i) =>
                  <ClassCard key={i} classData={c} showModal={showModal}/>
                )}
                <Card elevation={2} sx={{margin:"20px 5px 0 5px", width:"450px", outline:"1px solid gainsboro"}}>
                  <CardContent sx={{display:"flex", justifyContent:"center"}}>
                    <Button
                      variant="outlined"
                      sx={{minWidth:"80px", height:"30px", margin:"30px"}}
                      onClick={handleAddClass}>
                      add class
                    </Button>
                  </CardContent>
                </Card>
              </Box>
            </CardContent>
          </Card>
        </Stack>
      </Container>
    </div>
  )
}