import React from 'react';

import { Box, Card, CardContent, Container, Typography } from '@mui/material';
import Link from '@mui/material/Link';
import Stack from '@mui/material/Stack';
import TopBar from '../components/TopBar';

export default function About() {
  return (
    <div>
      <TopBar />
      <Container>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={5}
        >
          <Card elevation={2} sx={{outline:"1px solid gainsboro"}}>
            <CardContent>
              <Typography variant={"h5"} component={"h5"} m={1}>
              About
              </Typography>
              <Box m={2}>
                <Typography variant={"p"} component={"p"} m={3}>
                  <Link href="https://scalescience.wested.org/about/">SCALE Science at WestEd</Link> works with
                  states, districts, researchers, and developers to make
                  assessment more meaningful and enjoyable for students and teachers. SCALE Science is a leading
                  innovator on the design of rigorous 3-dimensional performance assessments and rubrics that
                  support teachers in transforming science instructional practice. The assessments form the
                  foundation of our practice-based professional learning on using performance assessments to an
                  equitable, engaging, and ambitious science classrooms.
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  This rubric website is based on a novel rubric format that we designed to help teachers make
                  sense of complex 3-dimensional student data. We created the <i>multidimensional rubric</i> model
                  primarily because we found all existing rubric models to be overly complex when used with
                  3-dimensional science standards and we felt that they offered insufficient actionable feedback.
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  Multidimensional rubrics are very different from typical rubrics, but when teachers learn to
                  use them they report that “this is the rubric I have been waiting for!”
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  <Link href="https://scalescience.wested.org/">Click here</Link> to learn more about us and to
                  explore the assessments related to these rubrics and our bank of free science performance
                  assessments and other resources.
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  <Link href="mailto:someone@example.com">Contact us</Link> for questions and to learn about our services.
                </Typography>
              </Box>
            </CardContent>
          </Card>
        </Stack>
      </Container>
    </div>
  )
}