import { createContext, useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';

import { cloneDeep } from 'lodash';
import { decompress as lzStringDeCompress } from 'lz-string';

import { defaultClassInfo, getUserInputFromClassList } from './utils/utils';
import { logText, logLocalStorageSize } from './utils/log-utils';
import { CLASS_INFO_KEY, USER_INPUT_KEY } from './utils/storage-utils';
import r1 from './data/rubric1.json';
import r2 from './data/rubric2.json';
import r3 from './data/rubric3.json';

import Home from './pages/Home';
import About from './pages/About';
import Guide from './pages/Guide';
import Privacy from './pages/Privacy';
import ClassManager from './pages/ClassManager';
import Rubric from './pages/Rubric';
import Options from './pages/Options';

const rubricList = [r1, r2, r3];

export const AppContext = createContext();

function App() {
  const [logEnabled, setLogEnabled] = useState(false);
  const [rubrics, setRubrics] = useState(rubricList);
  const [classInfo, setClassInfo] = useState(defaultClassInfo);
  const [currentClassID, setCurrentClassID] = useState(defaultClassInfo[0].id);
  const [currentRubricID, setCurrentRubricID] = useState(rubricList[0].r_id);
  const [currentStudentID, setCurrentStudentID] = useState(defaultClassInfo[0].students[0].id);
  const [currentUserInput, setCurrentUserInput] = useState({});

  useEffect(() => {
    setCurrentStudentID(classInfo[0].students[0].id);
  }, [classInfo]);

  useEffect(() => {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const log = urlParams.has("log");
    setLogEnabled(log);

    logLocalStorageSize(log);

    const storedClassInfo = lzStringDeCompress(localStorage.getItem(CLASS_INFO_KEY));
    let storedClassInfoJSON = undefined;
    if (storedClassInfo) {
      logText(log, "APP: found class info in local storage");
      storedClassInfoJSON = JSON.parse(storedClassInfo);
      if (storedClassInfoJSON && Array.isArray(storedClassInfoJSON) &&
          storedClassInfoJSON[0].hasOwnProperty("id") &&
          storedClassInfoJSON[0].hasOwnProperty("name") &&
          storedClassInfoJSON[0].hasOwnProperty("students")) {
        logText(log, "APP: class info is valid");
        logText(log, "APP: total classes found = " + storedClassInfoJSON.length);
        logText(log, storedClassInfoJSON);
        setClassInfo(storedClassInfoJSON);
        setCurrentClassID(storedClassInfoJSON[0].id);
        setCurrentStudentID(storedClassInfoJSON[0].students[0].id);
      }
    }
    // Now that we have the class info AND the rubrics,
    // we can make a object to hold the user input for each student
    const currClassList = storedClassInfoJSON ? storedClassInfoJSON : defaultClassInfo;
    const userInput = cloneDeep(getUserInputFromClassList(currClassList, rubricList));

    // Now we can grab the user input from local storage
    // and use it to populate our object
    const storedUserInput = lzStringDeCompress(localStorage.getItem(USER_INPUT_KEY));
    if (storedUserInput) {
      logText(log, "APP: found user input in local storage");
      const storedUserInputJSON = JSON.parse(storedUserInput);
      if (storedUserInputJSON && storedUserInputJSON.hasOwnProperty("cls")) {
        logText(log, "APP: user input is valid");
        logText(log, "APP: total user input class entries found = " + Object.keys(storedUserInputJSON.cls).length);
        logText(log, storedUserInputJSON);
        const classList = storedUserInputJSON.cls;
        for (const c in classList) {
          const citem = classList[c];
          for (const s in citem) {
            const sitem = citem[s];
            for (const r in sitem) {
              const ritem = sitem[r];
              for (const q in ritem) {
                const qitem = ritem[q];
                for (const d in qitem) {
                  const ditem = qitem[d];
                  for (const p in ditem) {
                    const pitem = ditem[p];
                    const userInputPitemVal = userInput.cls?.[c]?.[s]?.[r]?.[q]?.[d]?.[p]?.v;
                    if (userInputPitemVal === 0) {
                      userInput.cls[c][s][r][q][d][p].v = pitem.v;
                    }
                  }
                  const userInputNotes = userInput.cls?.[c]?.[s]?.[r]?.[q]?.[d]?.n;
                  if (userInputNotes !== null && userInputNotes !== undefined) {
                    userInput.cls[c][s][r][q][d].n = ditem.n;
                  }
                }
              }
            }
          }
        }
      }
    }
    setCurrentUserInput(userInput);
  }, []);

  return (
    <AppContext.Provider
      value={{
        rubricsContext: [rubrics, setRubrics],
        classInfoContext: [classInfo, setClassInfo],
        currentClassIDContext: [currentClassID, setCurrentClassID],
        currentRubricIDContext: [currentRubricID, setCurrentRubricID],
        currentStudentIDContext: [currentStudentID, setCurrentStudentID],
        currentUserInputContext: [currentUserInput, setCurrentUserInput],
        logContext: [logEnabled, setLogEnabled],
      }}
    >
      <Router>
        <Routes>
          <Route exact path="/" element={<Home/>} />
          <Route exact path="/class" element={<ClassManager/>} />
          <Route exact path="/about" element={<About/>} />
          <Route exact path="/guide" element={<Guide/>} />
          <Route exact path="/privacy" element={<Privacy/>} />
          <Route exact path="/options" element={<Options/>} />
          <Route exact path="/rubric/:id" element={<Rubric/>} />
        </Routes>
      </Router>
    </AppContext.Provider>
  );
}

export default App;
