import React from 'react';

import { Box, Card, CardContent, Container, Typography } from '@mui/material';
import Stack from '@mui/material/Stack';
import TopBar from '../components/TopBar';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';

export default function Privacy() {
  return (
    <div>
      <TopBar />
      <Container>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={5}
        >
          <Card elevation={2} sx={{outline:"1px solid gainsboro"}}>
            <CardContent>
              <Typography variant={"h5"} component={"h5"} m={1}>
                Privacy Policy
              </Typography>
              <Box m={2}>
                <Typography variant={"p"} component={"p"} m={3}>
                  Please read the following disclaimer carefully before using the SCALE Science Multidimensional Rubric ("Application").
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  By using this Application, you, the teacher, acknowledge that you may be required to locally store data
                  on your device as part of the functionality of the Application. This data may include sensitive and personally
                  identifiable student information that is protected under the Family Educational Rights and Privacy Act (FERPA).
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  As a user of this Application, it is your responsibility to ensure that any student data stored on your device
                  is secured and protected by reasonable methods to prevent unauthorized access, disclosure, or misuse of student information.
                  You are expected to comply with all applicable federal, state, and local laws regarding the protection of student data, as well
                  as adhere to the specific policies and procedures established by your school district around the use of technology and the
                  handling of educational records.
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  You are responsible for:
                </Typography>

                <List sx={{ listStyleType:"disc" }}>
                  <ListItem sx={{ marginLeft:"50px", display:"list-item" }}>Maintaining the confidentiality and security of student data within the Application.</ListItem>
                  <ListItem sx={{ marginLeft:"50px", display:"list-item" }}>Ensuring that access to the data is limited to authorized individuals only.</ListItem>
                  <ListItem sx={{ marginLeft:"50px", display:"list-item" }}>Implementing and following appropriate security measures, such as password protection and device encryption, to safeguard the data stored on your device.</ListItem>
                  <ListItem sx={{ marginLeft:"50px", display:"list-item" }}>Regularly backing up and updating security features on your device to protect against data loss or breaches.</ListItem>
                  <ListItem sx={{ marginLeft:"50px", display:"list-item" }}>Promptly reporting any suspected or actual unauthorized access to student data to the appropriate school district officials.</ListItem>
                </List>

                <Typography variant={"p"} component={"p"} m={3}>
                  Please consult with your school district's technology department or data protection officer if you have any questions regarding
                  best practices for data security or if you need assistance in ensuring that you are in compliance with district policies.
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  By continuing to use the Application, you acknowledge that you understand your responsibilities and agree to abide by these terms.
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  SCALE Science Multidimensional Rubric and its developers are not responsible for any failure to adhere to the above-stated responsibilities or for any
                  unauthorized access to student data resulting from such failure. It is your duty to protect the privacy and security of student
                  information at all times.
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  Thank you for your attention to this important matter and for your commitment to safeguarding your students' educational records.
                </Typography>

              </Box>
            </CardContent>
          </Card>
        </Stack>
      </Container>
    </div>
  )
}