import React, { useContext, useMemo } from 'react';
import { ScrollSyncPane } from 'react-scroll-sync';

import parse from 'html-react-parser';
import { cloneDeep } from 'lodash';
import { compress as lzStringCompress } from 'lz-string';

import { Box, Typography } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

import { alphabetizeStudentList, convertEntitiesToElements } from '../utils/utils';
import { USER_INPUT_KEY } from '../utils/storage-utils';

import { AppContext } from '../App';

import NoteEntry from './NoteEntry';
import PointCheckbox from './PointCheckbox';

const DimensionBlock = ({currentClassID, currentStudentID, currentRubricID, currentQuestionID,
  dimensionID, dimension, dimName, dimColor, highlight, allStudents, useAlphaStudents}) => {
  const { currentUserInputContext, classInfoContext } = useContext(AppContext);
  const [currentUserInput, setCurrentUserInput] = currentUserInputContext;
  const [classInfo] = classInfoContext;

  const handleChangeCheckbox = (event, studentID, pointID) => {
    let updatedCurrentUserInput = cloneDeep(currentUserInput);
    const updatedPoint = event.target.checked ? "1" : "0";
    updatedCurrentUserInput.cls[currentClassID][studentID][currentRubricID][currentQuestionID][dimensionID][pointID].v = updatedPoint;
    setCurrentUserInput(updatedCurrentUserInput);
    localStorage.setItem(USER_INPUT_KEY, lzStringCompress(JSON.stringify(updatedCurrentUserInput)));
  };

  const handleChangeNotes = (event) => {
    let updatedCurrentUserInput = cloneDeep(currentUserInput);
    const updatedNotes = event.target.value;
    updatedCurrentUserInput.cls[currentClassID][currentStudentID][currentRubricID][currentQuestionID][dimensionID].n = updatedNotes;
    setCurrentUserInput(updatedCurrentUserInput);
    localStorage.setItem(USER_INPUT_KEY, lzStringCompress(JSON.stringify(updatedCurrentUserInput)));
  };

  const cIndex = classInfo.findIndex((c) => c.id === currentClassID);
  let alphaStudents = useMemo(() => {
    return alphabetizeStudentList(classInfo[cIndex].students);
  }, [classInfo, cIndex]);

  const studentArray = useAlphaStudents ? alphaStudents : classInfo[cIndex].students;

  return (
    <Box sx={{marginBottom: "4px", border: `2px solid ${highlight ? dimColor : "white"}`, borderRadius: "4px"}}>
      <Accordion disableGutters>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="dimension-alignment-content"
          id="dimension-alignment-header111"
          sx={{fontWeight: "500", "&.MuiAccordionSummary-root": {minHeight: "36px", margin: "0"}, "&.MuiAccordionSummary-content": {margin: "0"}}}
        >
          {`Dimension Alignment: ${dimName}`}
        </AccordionSummary>
        <AccordionDetails sx={{padding:"4px 16px 8px 16px", fontSize:"14px", whiteSpace: "pre-line"}}>
          {parse(convertEntitiesToElements(dimension.dimension_alignment))}
        </AccordionDetails>
      </Accordion>
      <ScrollSyncPane>
        <Box
          sx={{ overflow:"auto", position:"relative", width: "100%", border: "1px solid lightgray",
                borderRadius: "4px", margin: "6px 0 0 0", padding:`${allStudents ? "8px 8px 8px 0" : "16px"}`}}
        >
          {!allStudents && dimension &&
          <>
            <Typography variant={"body2"} component={"div"}>
              {parse(convertEntitiesToElements(dimension.description_performance.prompt))}
            </Typography>
            {dimension.description_performance.points.map((p) =>
              <Box
                key={`si${currentStudentID}-qi${currentQuestionID}-di${dimensionID}-point${p.p_id}`}
                sx={{margin: "5px 0 4px 0"}}
                display="flex"
                alignItems={'flex-start'}
              >
                <Box sx={{padding: "2px 10px 0 0"}}>
                  <Typography variant={"body2"} component={"div"} sx={{fontSize:".85rem"}}>
                    {parse(convertEntitiesToElements(p.content))}
                  </Typography>
                </Box>
                <PointCheckbox
                  currentUserInput={currentUserInput}
                  classID={currentClassID}
                  studentID={currentStudentID}
                  rubricID={currentRubricID}
                  questionID={currentQuestionID}
                  dimensionID={dimensionID}
                  pointID={p.p_id}
                  handleChangeCheckbox={handleChangeCheckbox}
                />
              </Box>
            )}
            {!allStudents && <NoteEntry
              currentUserInput={currentUserInput}
              classID={currentClassID}
              studentID={currentStudentID}
              rubricID={currentRubricID}
              questionID={currentQuestionID}
              dimensionID={dimensionID}
              handleChangeNotes={handleChangeNotes}
            />}
          </>}


          {allStudents && dimension &&
          <table style={{borderCollapse:"collapse", width:"max-content"}}>
            <tbody>
            <tr>
              <td width= "250px" style={{verticalAlign:"bottom", position:"sticky", left:"-1px", backgroundColor:"white", paddingLeft:"8px"}}>
                <Typography variant={"body2"} component={"div"} sx={{fontSize:".8rem", padding:"4px"}}>
                  {parse(convertEntitiesToElements(dimension.description_performance.prompt))}
                </Typography>
              </td>
              {studentArray.map((s, i) =>
                <td key={`student${i}`} width="75px" align="center" style={{verticalAlign:"bottom"}}>
                  <Typography variant={"body2"} component={"div"} style={{fontSize:".75rem", padding:"4px"}}>
                    {s.name}
                  </Typography>
                </td>
              )}
            </tr>
            {dimension.description_performance.points.map((p) =>
              <tr
                key={`si${currentStudentID}-qi${currentQuestionID}-di${dimensionID}-point${p.p_id}`}
                style={{border:"1px solid lightgray"}}
              >
                <td width= "250px" style={{position:"sticky", left:"-1px", backgroundColor:"white", paddingLeft:"8px", zIndex:"10"}}>
                  <Typography variant={"body2"} component={"div"} sx={{fontSize:".8rem", padding:"4px"}}>
                    {parse(convertEntitiesToElements(p.content))}
                  </Typography>
                </td>
                {studentArray.map((s, i) =>
                  <td key={`student${i}`} width="75px" align="center">
                    <PointCheckbox
                      currentUserInput={currentUserInput}
                      classID={currentClassID}
                      studentID={s.id}
                      rubricID={currentRubricID}
                      questionID={currentQuestionID}
                      dimensionID={dimensionID}
                      pointID={p.p_id}
                      handleChangeCheckbox={handleChangeCheckbox}
                    />
                  </td>
                )}
              </tr>
            )}
            </tbody>
          </table>}
        </Box>
      </ScrollSyncPane>
    </Box>
  );
}

export default DimensionBlock;