import React, { useContext } from 'react';

import { Box, Typography } from '@mui/material';

import { AppContext } from '../App';

const GroupSummary = ({performanceLevel, currentRubric, currentClass}) => {
  const { currentUserInputContext } = useContext(AppContext);
  const [currentUserInput] = currentUserInputContext;

  let groupQuestionIds = [];
  let studentScores = [];
  // iterate over each question in the rubric and get a list of individual question ids
  for (let q = 0; q < currentRubric.questions.length; q++) {
    if (currentRubric.questions[q].is_group) {
      const qid = currentRubric.questions[q].q_id;
      groupQuestionIds.push(qid);
    }
  }

  // iterate over each student's entries in the current user input for this class
  for (const [key, value] of Object.entries(currentUserInput.cls[currentClass.id])) {
    const rubricEntry = value[currentRubric.r_id];
    let totalScore = 0;
    // iterate over the questions and see if they are individual questions
    for (const [rkey, rvalue] of Object.entries(rubricEntry)) {
      if (groupQuestionIds.findIndex(el => el === rkey) >= 0) {
        // now we need to look at all the dimensions and values and summarize the score
        for (const [, dvalue] of Object.entries(rvalue)) {
          // each entry value for each dimension (ignore notes)
          for (const [, evalue] of Object.entries(dvalue)) {
            const entryScore = evalue?.v || 0;
            totalScore += Number(entryScore);
          }
        }
      }
    }
    const student = currentClass.students.find((el) => el.id === Number(key));
    const studentScore = {id: key, name: student.name, score: totalScore};
    studentScores.push(studentScore);
  }

  return (
    <Box p={4} sx={{width: "100%", border: "1px solid lightgray", borderRadius: "4px", "&.MuiBox-root": {margin: "4px"}}}>
      <Typography variant={"h6"} component={"h6"}>Group Student Scores and Performance Levels</Typography>

      <Box
        sx={{ overflow:"auto", position:"relative", width: "100%", border: "1px solid lightgray",
              borderRadius: "4px", margin: "20px 0 20px 0", padding:"16px"}}
      >
        <table style={{borderCollapse:"collapse", width:"max-content"}}>
          <tbody>
            <tr>
              {studentScores.map((s, i) =>
                <td key={`studentname${i}`} width="75px" align="center"
                    style={{verticalAlign:"bottom", backgroundColor:"#F0F0F0", border: "1px solid lightgray"}}>
                  <Typography variant={"body2"} component={"div"} style={{fontSize:".75rem", padding:"4px"}}>
                    {s.name}
                  </Typography>
                </td>
              )}
            </tr>
            <tr>
              {studentScores.map((s, i) =>
                <td key={`studentscore${i}`} width="75px" align="center" style={{verticalAlign:"bottom", border: "1px solid lightgray"}}>
                  <Typography variant={"body2"} component={"div"} style={{fontSize:".75rem", padding:"4px"}}>
                    {s.score}
                  </Typography>
                </td>
              )}
            </tr>
          </tbody>
        </table>
      </Box>

      <Box p={2}>
        <Typography variant={"body2"} component={"div"} sx={{fontWeight: 800}}>{performanceLevel.summary_header}</Typography>
        {performanceLevel.summary_items.map((si, i) =>
          <Typography key={`pl-si-${i}`} variant={"body2"} component={"div"}>{si}</Typography>
        )}
      </Box>
      {performanceLevel.performance_levels.map((pi, i) =>
        <Box key={`pi-si-${i}`} p={2}>
          <Typography variant={"body2"} component={"div"} sx={{fontWeight: 800}}>
            {`${pi.header} (${pi.min_points}${pi.min_points !== pi.max_points ? " - " + pi.max_points: ""} points)`}
          </Typography>
          <Typography variant={"body2"} component={"div"}>
            {pi.content}
          </Typography>
        </Box>
      )}
    </Box>
  );
}

export default GroupSummary;