import Checkbox from '@mui/material/Checkbox';

const PointCheckbox = ({currentUserInput, classID, studentID, rubricID, questionID, dimensionID, pointID, handleChangeCheckbox}) => {
  return (
    <Checkbox
      key={`si${studentID}-qi${questionID}-di${dimensionID}-point${pointID}`}
      sx={{padding: "0 4px 0 0"}}
      checked={currentUserInput?.cls?.[classID]?.[studentID]?.[rubricID]?.[questionID]?.[dimensionID]?.[pointID]?.v === "1"}
      inputProps={{ 'aria-label': 'controlled' }}
      onChange={(event) => {
        handleChangeCheckbox(event, studentID, pointID);
      }}
    />
  );
}

export default PointCheckbox;