import * as React from 'react';
import { Button } from '@mui/material';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const ChoiceDialog = ({open, handleClose, headerText, bodyText, noText, yesText}) => {
  return (
    <Dialog
      open={open}
      TransitionComponent={Transition}
      keepMounted
      onClose={() => handleClose(false)}
      aria-describedby="choice-dialog-slide-description"
    >
      <DialogTitle>{headerText}</DialogTitle>
      <DialogContent>
        <DialogContentText id="choice-dialog-slide-description">
          {bodyText}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={() => handleClose(false)}>{noText}</Button>
        <Button onClick={() => handleClose(true)}>{yesText}</Button>
      </DialogActions>
    </Dialog>
  );
}

export default ChoiceDialog;