import React, { useEffect, useState } from 'react';
import { Button } from '@mui/material';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import TextField from '@mui/material/TextField';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const StudentListDialog = ({open, handleClose}) => {
  const [studentList, setStudentList] = useState("");
  const [studentListLength, setStudentListLength] = useState(3);

  useEffect(() => {
    if (!open) {
      setStudentList("");
      setStudentListLength(3);
    }
  }, [open]);

  const handleChangeStudentList = (event) => {
    setStudentList(event.target.value);
    const studentArray = event.target.value.split("\n");
    setStudentListLength(studentArray.length);
  };

  return (
    <Dialog
      open={open}
      TransitionComponent={Transition}
      keepMounted
      onClose={() => handleClose(false, "")}
      aria-describedby="student-list-dialog-slide-description"
      fullWidth={true}
    >
      <DialogTitle>Add Students</DialogTitle>
      <DialogContent>
        <DialogContentText id="student-list-dialog-slide-description">
          Enter a list of student names (put each student name on a new line):
        </DialogContentText>
        <TextField
          id="student-list"
          variant="outlined"
          multiline
          maxRows={studentListLength}
          sx={{width: "100%"}}
          inputProps={{style: {fontSize: "0.85rem"}}}
          value={studentList}
          onChange={(event) => {
            handleChangeStudentList(event)
          }}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={() => handleClose(false, "")}>Cancel</Button>
        <Button onClick={() => handleClose(true, studentList)}>Add Students</Button>
      </DialogActions>
    </Dialog>
  );
}

export default StudentListDialog;