import React from 'react';

import { Box, Card, CardContent, Container, Typography } from '@mui/material';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Link from '@mui/material/Link';
import Stack from '@mui/material/Stack';

import TopBar from '../components/TopBar';

const faqText = [
  {summary: "How can I see my data from a different computer?", content: "coming soon..."},
  {summary: "Why can’t I see my entries from a previous session?", content: "coming soon..."},
  {summary: "How do I assign scores to my students?", content: "coming soon..."},
  {summary: "Can I print or download my class data?", content: "coming soon..."},
  {summary: "How do I communicate about the data from the rubric to students and caregivers?", content: "coming soon..."},
  {summary: "How can I figure out grades for my students using the rubric scores?", content: "coming soon..."},
  {summary: "Where can I find more information about these assessments and rubrics?", content: "coming soon..."},
  {summary: "Who do I contact if I encounter a technical issue with the website?", content: "coming soon..."},
]

export default function Guide() {
  return (
    <div>
      <TopBar />
      <Container>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={5}
        >
          <Card elevation={2} sx={{outline:"1px solid gainsboro"}}>
            <CardContent>
              <Typography variant={"h5"} component={"h5"} m={1}>
                How to use the SCALE Multidimensional Rubric App
              </Typography>

              <Box m={2}>
                <Typography variant={"p"} component={"p"} m={3}>
                  This app was designed to help you efficiently make sense of student work from 3-dimensional
                  performance assessments and to guide responsive instructional decisions based on trends.
                  It’s pretty different from other rubrics!
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  <Link href="#">Watch this video</Link> to learn more about how to use this app most
                  effectively or follow the instructions below:
                </Typography>

                <Typography variant={"p"} component={"p"} m={3}>
                  Instructions: coming soon...
                </Typography>
              </Box>

              <Typography variant={"h5"} component={"h5"} m={1}>
                FAQs
              </Typography>
              <Box m={2}>
                {faqText.map((ft, i) =>
                  <Accordion key={`faq-${i}`} disableGutters>
                    <AccordionSummary
                      key={`faq-as-${i}`}
                      expandIcon={<ExpandMoreIcon />}
                      aria-controls="faq-content"
                      sx={{fontWeight:"500"}}
                    >
                      {ft.summary}
                    </AccordionSummary>
                    <AccordionDetails key={`faq-ad-${i}`} sx={{whiteSpace:"pre-line"}}>
                      {ft.content}
                    </AccordionDetails>
                  </Accordion>
                )}
              </Box>

            </CardContent>
          </Card>
        </Stack>
      </Container>
    </div>
  )
}