import React, { useContext, useState }  from 'react';

import { cloneDeep } from 'lodash';

import { Box, Button, Card, CardContent, Container, Typography } from '@mui/material';
import Stack from '@mui/material/Stack';
import TopBar from '../components/TopBar';

import { clearStorageData } from '../utils/storage-utils';
import { defaultClassInfo, getUserInputFromClassList } from '../utils/utils';
import { logText } from '../utils/log-utils';

import { AppContext } from '../App';

import ChoiceDialog from '../components/ChoiceDialog';
import ModalPopUp from '../components/ModalPopUp';

export default function Options() {
  const { classInfoContext, currentClassIDContext, currentStudentIDContext, currentUserInputContext, rubricsContext, logContext } = useContext(AppContext);
  const [, setClassInfo] = classInfoContext;
  const [, setCurrentClassID] = currentClassIDContext;
  const [, setCurrentStudentID] = currentStudentIDContext;
  const [, setCurrentUserInput] = currentUserInputContext;
  const [rubricList] = rubricsContext;
  const [logEnabled] = logContext;

  const [openModal, setOpenModal] = useState(false);
  const handleOpenModal = () => setOpenModal(true);
  const handleCloseModal = () => setOpenModal(false);

  const handleClearStorage = () => {
    logText(logEnabled, "OPTIONS: clear local storage");
    clearStorageData();
    setClassInfo(defaultClassInfo);
    setCurrentClassID(defaultClassInfo[0].id);
    setCurrentStudentID(defaultClassInfo[0].students[0].id);

    const userInput = cloneDeep(getUserInputFromClassList(defaultClassInfo, rubricList));
    setCurrentUserInput(userInput);
    handleOpenModal();
  }

  const [showDialog, setShowDialog] = useState(false);

  const handleShowDialog = () => {
    setShowDialog(true);
  };
  const handleCloseDialog = (accept) => {
    if (accept) handleClearStorage();
    setShowDialog(false);
  };

  return (
    <div>
      <ModalPopUp
        open={openModal}
        handleClose={handleCloseModal}
        headerText={"Session Data Deleted"}
        bodyText={"All session class, student roster, and rubric input data has been deleted."}
      />
      <ChoiceDialog
        headerText={"Delete Session Data"}
        bodyText={"Are you sure you want to delete session class, student roster, and rubric input data?"}
        yesText="YES"
        noText={"CANCEL"}
        open={showDialog}
        handleClose={handleCloseDialog}
      />
      <TopBar />
      <Container>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={5}
        >
          <Card elevation={2} sx={{outline:"1px solid gainsboro"}}>
            <CardContent>
              <Typography variant={"h5"} component={"h5"} m={1}>
                Options and Settings
              </Typography>
              <Box m={2}>
                <p></p>
              </Box>
              <Box p={2} display="flex" flexDirection="row" flexWrap={"wrap"}>
                <Card elevation={2} sx={{margin:"20px 5px 0 5px", width:"450px", outline:"1px solid gainsboro"}}>
                  <CardContent sx={{display:"flex", flexDirection:"column", justifyContent:"center"}}>
                    <Typography variant={"h6"} component={"h6"} m={1}>Delete Session Data</Typography>
                    <Typography variant={"p"} component={"p"} m={1}>Delete all classes, students, and rubric input data. <b>WARNING: once this data is deleted, it CANNOT be restored.</b></Typography>
                    <Button
                      variant="outlined"
                      sx={{minWidth:"80px", height:"30px", margin:"30px"}}
                      onClick={handleShowDialog}>
                      delete
                    </Button>
                  </CardContent>
                </Card>
              </Box>
            </CardContent>
          </Card>
        </Stack>
      </Container>
    </div>
  )
}