import { cloneDeep } from 'lodash';

export const dimensionColors = ["#0071C2", "#7D9D38", "#FF9801"];
export const dimensionNames = ["Practices", "Crosscutting", "Core Ideas"];
export const dimensionFullNames = ["Science and Engineering Practices", "Crosscutting Concepts", "Disciplinary Core Ideas"];

export const defaultStudentList = [{id: 0, name: "student a"}, {id: 1, name: "student b"}, {id: 2, name: "student c"}];
export const defaultClassInfo = [
  {id: 0, name: "my class", students: [{id: 0, name: "student a"}, {id: 1, name: "student b"}, {id: 2, name: "student c"}]}
];
export const defaultClass = {id: 0, name: "my class", students: [{id: 0, name: "student a"}, {id: 1, name: "student b"}, {id: 2, name: "student c"}]};

export const convertEntitiesToElements = (text) => {
  const updatedText = text.replaceAll("&lt;", "<").replaceAll("&gt;", ">");
  return updatedText;
};

export const getUserInputFromClassList = (classList, rubricList) => {
  let userInput = { cls: {} };
  for (let c = 0; c < classList.length; c++) {
    const citem = classList[c];
    userInput.cls[citem.id] = {};
    for (let s = 0; s < citem.students.length; s++) {
      const sitem = citem.students[s];
      userInput.cls[citem.id][sitem.id] = {};
      for (let r = 0; r < rubricList.length; r++) {
        const ritem = rubricList[r];
        userInput.cls[citem.id][sitem.id][ritem.r_id] = {};
        for (let q = 0; q < ritem.questions.length; q++) {
          const qitem = ritem.questions[q];
          userInput.cls[citem.id][sitem.id][ritem.r_id][qitem.q_id] = {};
          for (let d = 0; d < qitem.dimensions.length; d++) {
            const ditem = qitem.dimensions[d];
            userInput.cls[citem.id][sitem.id][ritem.r_id][qitem.q_id][ditem.d_id] = { n: "" };
            for (let p = 0; p < ditem.description_performance.points.length; p++) {
              const pitem = ditem.description_performance.points[p];
              userInput.cls[citem.id][sitem.id][ritem.r_id][qitem.q_id][ditem.d_id][pitem.p_id] = { v: 0};
            }
          }
        }
      }
    }
  }
  return userInput;
};

export const alphabetizeStudentList = (studentList) => {
  let studentListCopy = cloneDeep(studentList);
  studentListCopy.sort((a, b) => {
    var textA = a.name.toUpperCase();
    var textB = b.name.toUpperCase();
    return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
  });
  return studentListCopy;
};