import { useState, useContext } from 'react';

import { cloneDeep } from 'lodash';
import { compress as lzStringCompress } from 'lz-string';

import { Box, Button, Card, CardContent, Typography } from '@mui/material';
import DoneIcon from '@mui/icons-material/Done';
import EditIcon from '@mui/icons-material/Edit';
import IconButton from '@mui/material/IconButton';
import TextField from '@mui/material/TextField';

import { CLASS_INFO_KEY, USER_INPUT_KEY } from '../utils/storage-utils';
import { getUserInputFromClassList, defaultClassInfo } from '../utils/utils';

import { AppContext } from "../App";

import ChoiceDialog from './ChoiceDialog';
import StudentItem from './StudentItem';
import StudentListDialog from './StudentListDialog';

const ClassCard = ({classData, showModal}) => {
  const { classInfoContext, currentClassIDContext, currentStudentIDContext, currentUserInputContext, rubricsContext } = useContext(AppContext);
  const [classInfo, setClassInfo] = classInfoContext;
  const [, setCurrentClassID] = currentClassIDContext;
  const [, setCurrentStudentID] = currentStudentIDContext;
  const [currentUserInput, setCurrentUserInput] = currentUserInputContext;
  const [rubrics] = rubricsContext;

  const [editingClassName, setEditingClassName] = useState(false);

  const handleDeleteClass = () => {
    const classId = classData.id;
    let updatedClassInfo = cloneDeep(classInfo);
    const classIndex = updatedClassInfo.findIndex((e) => e.id === classId);
    updatedClassInfo.splice(classIndex, 1);

    let updatedCurrentUserInput = { cls: {} };
    for (const property in currentUserInput.cls) {
      if (property !== classId) {
        updatedCurrentUserInput.cls[property] = cloneDeep(currentUserInput.cls[property]);
      }
    }

    if (updatedClassInfo.length === 0) {
      setClassInfo(defaultClassInfo);
      setCurrentClassID(defaultClassInfo[0].id);
      setCurrentStudentID(defaultClassInfo[0].students[0].id);

      const userInput = cloneDeep(getUserInputFromClassList(defaultClassInfo, rubrics));
      setCurrentUserInput(userInput);

      localStorage.setItem(CLASS_INFO_KEY, lzStringCompress(JSON.stringify(defaultClassInfo)));
      localStorage.setItem(USER_INPUT_KEY, lzStringCompress(JSON.stringify(userInput)));

      showModal("Default Class Added", "The last class has been deleted. A new default class has been added.");
    } else {
      setCurrentClassID(updatedClassInfo[0].id);
      setCurrentStudentID(updatedClassInfo[0].students[0].id);
      setClassInfo(updatedClassInfo);
      setCurrentUserInput(updatedCurrentUserInput);

      localStorage.setItem(CLASS_INFO_KEY, lzStringCompress(JSON.stringify(updatedClassInfo)));
      localStorage.setItem(USER_INPUT_KEY, lzStringCompress(JSON.stringify(updatedCurrentUserInput)));

      showModal("Class Deleted", "The class, student roster, and rubric input data has been deleted.");
    }
  }

  const handleAddStudentList = (studentList) => {
    const studentArray = studentList.split("\n");
    if (!studentArray || studentArray.length === 0)
      return;
    let updatedStudentList = [];
    let maxId = 0;
    // make a new object with all of the new students
    for (let i = 0; i < classData.students.length; i++) {
      maxId = classData.students[i].id > maxId ? classData.students[i].id : maxId;
      const currStudent = cloneDeep(classData.students[i]);
      updatedStudentList.push(currStudent);
    }
    for (let i = 0; i < studentArray.length; i++) {
      maxId++;
      const s = { id: maxId, name: studentArray[i]};
      updatedStudentList.push(s);
    }

    let updatedClassInfo = cloneDeep(classInfo);
    let cIndex = classInfo.findIndex((c) => c.id === classData.id);
    updatedClassInfo[cIndex].students = updatedStudentList;
    setClassInfo(updatedClassInfo);
    localStorage.setItem(CLASS_INFO_KEY, lzStringCompress(JSON.stringify(updatedClassInfo)));
    addNewStudentsToUserInput(updatedClassInfo);
  };

  const handleAddStudent = () => {
    let updatedStudentList = cloneDeep(classData.students);
    let maxId = 0;
    // find the starting point of any new student ids
    for (let i = 0; i < updatedStudentList.length; i++) {
      maxId = updatedStudentList[i].id > maxId ? updatedStudentList[i].id : maxId;
    }
    maxId++;
    const newStudent = { id: maxId, name: "new student"};
    updatedStudentList.push(newStudent);

    let updatedClassInfo = cloneDeep(classInfo);
    let cIndex = classInfo.findIndex((c) => c.id === classData.id);
    updatedClassInfo[cIndex].students = updatedStudentList;
    setClassInfo(updatedClassInfo);
    localStorage.setItem(CLASS_INFO_KEY, lzStringCompress(JSON.stringify(updatedClassInfo)));

    addNewStudentsToUserInput(updatedClassInfo);
  };

  const addNewStudentsToUserInput = (updatedClassInfo) => {
    let cIndex = classInfo.findIndex((c) => c.id === classData.id);
    let updatedCurrentUserInput = cloneDeep(currentUserInput);
    updatedCurrentUserInput.cls[classData.id] = {};
    for (let s = 0; s < updatedClassInfo[cIndex].students.length; s++) {
      const sitem = updatedClassInfo[cIndex].students[s];
      updatedCurrentUserInput.cls[classData.id][sitem.id] = {};
      for (let r = 0; r < rubrics.length; r++) {
        const ritem = rubrics[r];
        updatedCurrentUserInput.cls[classData.id][sitem.id][ritem.r_id] = {};
        for (let q = 0; q < ritem.questions.length; q++) {
          const qitem = ritem.questions[q];
          updatedCurrentUserInput.cls[classData.id][sitem.id][ritem.r_id][qitem.q_id] = {};
          for (let d = 0; d < qitem.dimensions.length; d++) {
            const ditem = qitem.dimensions[d];
            // add in existing notes if they exist
            const userInputNotes = currentUserInput.cls?.[cIndex]?.[sitem.id]?.[ritem.r_id]?.[qitem.q_id]?.[ditem.d_id]?.n || "";
            updatedCurrentUserInput.cls[classData.id][sitem.id][ritem.r_id][qitem.q_id][ditem.d_id] = { n: userInputNotes };
            for (let p = 0; p < ditem.description_performance.points.length; p++) {
              const pitem = ditem.description_performance.points[p];
              const userInputPitemVal = currentUserInput.cls?.[cIndex]?.[sitem.id]?.[ritem.r_id]?.[qitem.q_id]?.[ditem.d_id]?.[pitem.p_id]?.v || 0;
              // add in existing inputs vals if they exist
              updatedCurrentUserInput.cls[classData.id][sitem.id][ritem.r_id][qitem.q_id][ditem.d_id][pitem.p_id] = {v: userInputPitemVal};
            }
          }
        }
      }
    }
    setCurrentUserInput(updatedCurrentUserInput);
    localStorage.setItem(USER_INPUT_KEY, lzStringCompress(JSON.stringify(updatedCurrentUserInput)));
  };

  const handleChangeClassName = (event) => {
    let updatedClassInfo = cloneDeep(classInfo);
    let cIndex = classInfo.findIndex((c) => c.id === classData.id);
    updatedClassInfo[cIndex].name = event.target.value;
    setClassInfo(updatedClassInfo);
    localStorage.setItem(CLASS_INFO_KEY, lzStringCompress(JSON.stringify(updatedClassInfo)));
  };

  const [showDialog, setShowDialog] = useState(false);
  const handleShowDialog = () => {
    setShowDialog(true);
  };
  const handleCloseDialog = (accept) => {
    if (accept) handleDeleteClass();
    setShowDialog(false);
  };

  const [showStudentListDialog, setShowStudentListDialog] = useState(false);
  const handleShowStudentListDialog = () => {
    setShowStudentListDialog(true);
  };
  const handleCloseStudentListDialog = (accept, studentList) => {
    if (accept) handleAddStudentList(studentList);
    setShowStudentListDialog(false);
  };

  return (
    <Card elevation={2} sx={{margin: "20px 5px 0 5px", width: "450px", outline: "1px solid gainsboro"}}>
      <ChoiceDialog
        headerText={"Delete Class"}
        bodyText={"Are you sure you want to delete this class?"}
        yesText="YES"
        noText={"CANCEL"}
        open={showDialog}
        handleClose={handleCloseDialog}
      />
      <StudentListDialog
        open={showStudentListDialog}
        handleClose={handleCloseStudentListDialog}
      />
      <CardContent>
        <Box p={2} sx={{border: "1px solid lightgray", borderRadius: "4px", "&.MuiBox-root": {margin: "0"}}}>
          <Typography variant={"h6"} component={"h6"}>
            Class Name
          </Typography>
          <Box
            sx={{margin:"2px", padding:"8px", justifyContent:"center", width:"100%"}}
            display="flex"
            flexDirection="row"
            alignItems="center"
          >
            <Box
              sx={{margin:"2px", justifyContent:"center"}}
              display="flex"
              flexDirection="row"
              alignItems="center"
            >
              {!editingClassName
              ? <Typography sx={{minWidth:"310px", borderBottom:"1px solid", fontSize:"0.8rem"}}>
                {classData.name}
              </Typography>
              : <TextField
                id="student-name"
                variant="outlined"
                size="small"
                value={classData.name}
                sx={{minWidth: "310px"}}
                inputProps={{style: {fontSize:"0.8rem", height:"11px"}}}
                onChange={(event) => {
                  handleChangeClassName(event);
                }}
              />}
            </Box>
            <Box
              sx={{margin:"2px", marginLeft:"auto"}}
              display="flex"
              flexDirection="row"
              alignItems="center"
            >
              <IconButton
                size="large"
                edge="start"
                color="black"
                aria-label="menu"
                sx={{margin:"2px 8px 2px 4px", padding:"0"}}
                onClick={() => setEditingClassName(!editingClassName)}
              >
                {editingClassName ? <DoneIcon /> : <EditIcon />}
              </IconButton>
            </Box>
          </Box>
        </Box>
        <Box p={2} sx={{border: "1px solid lightgray", borderRadius: "4px", "&.MuiBox-root": {margin: "4px 0 0 0"}}}>
          <Typography variant={"h6"} component={"h6"}>
            Student Roster
          </Typography>
          <Box p={1} sx={{"&.MuiBox-root": {margin: "2px"}}} display="flex" flexDirection="column" alignItems="flex-start">
            {classData.students.map((s, i) =>
                <StudentItem
                  key={`student-${s.id}`}
                  studentName={s.name}
                  studentId={s.id}
                  classId={classData.id}
                  disableDelete={classData.students.length === 1}
                />)
            }
          </Box>
          <Box display="flex" justifyContent="flex-end" flexDirection="row">
            <Button
              variant="outlined"
              sx={{minWidth:"60px", height:"24px", margin:"20px 4px 4px 4px", fontSize:"12px"}}
              onClick={handleShowStudentListDialog}
            >
              add student list
            </Button>
            <Button
              variant="outlined"
              sx={{minWidth:"60px", height:"24px", margin:"20px 4px 4px 4px", fontSize:"12px"}}
              onClick={handleAddStudent}
            >
              add student
            </Button>
          </Box>
        </Box>
        <Box display="flex" justifyContent="flex-end">
          <Button
            variant="outlined"
            sx={{minWidth:"60px", height:"24px", margin:"20px 4px 4px 4px", fontSize:"12px"}}
            onClick={handleShowDialog}
          >
            delete class
          </Button>
        </Box>
      </CardContent>
    </Card>
  );
}

export default ClassCard;