import React from 'react';
import { useContext } from 'react';
import { useNavigate } from 'react-router-dom';

import { Box, Button, Card, CardContent, Container, Typography } from '@mui/material';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import Link from '@mui/material/Link';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';

import { AppContext } from '../App';

import TopBar from '../components/TopBar';

export default function Home() {
  let navigate = useNavigate();
  const navigateToStart = () =>{
    let path = `/class`;
    navigate(path);
  }
  const navigateToRubric = () =>{
    let path = `/rubric/${currentRubricID}`;
    navigate(path);
  }

  const { classInfoContext, currentRubricIDContext, rubricsContext, currentClassIDContext, currentStudentIDContext } = useContext(AppContext);
  const [classInfo] = classInfoContext;
  const [rubrics] = rubricsContext;
  const [currentRubricID, setCurrentRubricID] = currentRubricIDContext;
  const [currentClassID, setCurrentClassID] = currentClassIDContext;
  const [, setCurrentStudentID] = currentStudentIDContext;

  const handleChangeRubric = (event) => {
    setCurrentRubricID(event.target.value);
  };

  const handleChangeClass = (event) => {
    const classID = event.target.value;
    setCurrentClassID(classID);
    setCurrentStudentID(classInfo[classID].students[0].id);
  };

  return (
    <div>
      <TopBar />
      <Container>
        <Stack
          spacing={{ xs: 1, sm: 2, md: 4 }}
          margin={5}
        >
          <Card elevation={2} sx={{outline:"1px solid gainsboro"}}>
            <CardContent>
              <Typography variant={"h5"} component={"h5"} m={1}>
                SCALE Science Multidimensional Rubric
              </Typography>
              <Box m={2}>
                <Typography variant={"body1"} component={"div"}>
                This website will help you analyze and score your student work on SCALE Science performance assessments.
                Once you record data about your students’ responses, it will also illuminate trends in strengths and needs across your class and next instructional
                steps that can support ongoing growth. The rubrics will guide you in using data from your students to identify ways to enhance an upcoming lesson
                or unit to efficiently keep working on these trends where they are relevant.
                </Typography>
                <Typography variant={"body1"} component={"div"} sx={{marginTop:"20px"}}>
                This early version of this app stores your entries on your computer (not the web) in order to protect your student data. This means that <b>you will
                need to use the same computer and browser</b> to retrieve data from an earlier session, or you can export the data and open that file anywhere.
                </Typography>
                <Typography variant={"body1"} component={"div"} sx={{marginTop:"20px"}}>
                If you have questions or feedback please <Link href="mailto:someone@example.com">contact us</Link>.
                </Typography>

              </Box>
              <Box m={5} display="flex" justifyContent="center" alignItems="flex-end" sx={{flexWrap:"wrap"}}>
                <Box display="flex" flexDirection="column" justifyContent="center" m={2} p={2} sx={{border:"1px solid lightgray", borderRadius:"4px"}}>
                  <Typography variant={"body2"} component={"div"} sx={{width:"220px", margin:"10px 20px 10px 20px", textAlign:"start"}}>
                    Create a class containing a list of students. You can create multiple classes.
                  </Typography>
                  <Button
                    variant="outlined"
                    sx={{width:"220px", margin:"10px 20px 10px 20px"}}
                    onClick={navigateToStart}
                  >
                    CLASS MANAGER
                  </Button>
                </Box>

                <Box display="flex" flexDirection="column" justifyContent="center" m={2} p={2} sx={{border:"1px solid lightgray", borderRadius:"4px"}}>
                  <Typography variant={"body2"} component={"div"} sx={{width:"220px", margin:"10px 20px 10px 20px", textAlign:"start"}}>
                    If you have already entered class information, choose a rubric to begin or continue entering data.
                  </Typography>
                  <Box
                    component="form"
                    sx={{
                      '& > :not(style)': { m: 1, width: '45ch' },
                    }}
                    display="flex"
                    justifyContent="center"
                  >
                    <FormControl style={{maxWidth:"220px"}} m={0}>
                      <InputLabel id="class-select-label">Class</InputLabel>
                      <Select
                        labelId="class-select-label"
                        id="class-select"
                        value={currentClassID}
                        label="Class"
                        onChange={handleChangeClass}
                      >
                        {classInfo.map((c, i) =>
                          <MenuItem key={`class${i}`} value={c.id}>{c.name}</MenuItem>
                        )}
                      </Select>
                    </FormControl>
                  </Box>
                  <Box
                    component="form"
                    sx={{
                      '& > :not(style)': { m: 1, width: '45ch' },
                    }}
                    display="flex"
                    justifyContent="center"
                  >
                    <FormControl style={{maxWidth:"220px"}} m={0}>
                      <InputLabel id="rubric-select-label">Rubric</InputLabel>
                      <Select
                        labelId="rubric-select-label"
                        id="rubric-select"
                        value={currentRubricID}
                        label="Rubric"
                        onChange={handleChangeRubric}
                      >
                        {rubrics.map((r, i) =>
                          <MenuItem key={`rubric${i}`} value={r.r_id}>{r.name}</MenuItem>
                        )}
                      </Select>
                    </FormControl>
                  </Box>
                  <Button
                    variant="outlined"
                    sx={{width:"220px", margin:"10px 20px 10px 20px"}}
                    onClick={navigateToRubric}
                  >
                    ENTER RUBRIC DATA
                  </Button>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Stack>
      </Container>
    </div>
  )
}